<template>
    <!-- -->
    <div class="app-text">
        <!--  -->
        <mt-header>
            <router-link to="set" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
            <p slot="right"></p>
        </mt-header>
        <!--  -->
        <div class="set-text">
            <div class="original">
                <span></span>
                <input v-model="phone" name="phone" type="password" placeholder="">
            </div>
            <div class="new-text">
                <span></span>
                <input v-model="phone1" name="phone1" type="password" placeholder="" @blur="textRet">
                <input v-model="phone2" name="phone2" type="password" placeholder="">
            </div>
            <div class="cancelT">
                <p @click="clearT"></p>
                <p @click="subT"></p>
            </div>
        </div>
    </div>
</template>
<script>
import {Toast} from "mint-ui"
export default {
    data(){
        return{
            phone:"",//
            phone1:"",//
            phone2:"",//
        }
    },
    methods:{
        // 
        textRet(){
            var url="retrieval?phone1="+this.phone1;
            this.axios.get(url).then(result=>{
                if(result.data.code==1){
                    Toast("");
                    this.phone1="";
                }
            })
        },
        subT(){
            var phone=this.phone;
            var phone1=this.phone1;
            var reg=/^1[3-8][0-9]{9}$/;
            if(!reg.test(this.phone1)){
                Toast("");
                return;
            }
            if(this.phone1!=this.phone2){
                Toast("");
                return;
            }
            var postData=this.qs.stringify({
                phone:phone,
                phone1:phone1
            })
            var url="text?phone="+phone+"&phone1="+this.phone1;
            this.axios.post(url,postData).then(result=>{
                if(result.data.code==1){
                    Toast("");
                    this.phone="";
                    this.phone1="";
                    this.phone2="";  
                }else{
                    Toast("")
                } 
            })
        },
        clearT(){
            this.phone="";
            this.phone1="";
            this.phone2="";
        },
    },
}
</script>
<style>
    /*  */
    .set-text{
        width: 85%;
        margin:0 auto;
        margin-top:40px;
    }
    /* span*/
    .set-text span{
        display:block;
        margin: 20px 0;
    }
    /* input */
    .set-text input{
        width: 100% !important;
        border-radius:7px !important;
    }
    /*  */
    .cancelT{
        width: 100%;
        display: inline-block;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top:20px;
    }
    .cancelT>p{
        width: 31%;
        padding: 13px 0;
        text-align: center;
        color:#333;
        font-size:20px;
    }
    .cancelT>p:first-child{
        background: #dcdcdc;
    }
    .cancelT>p:last-child{
        background: #2ad181bd;
    }
</style>



