<template>
    <!--  -->
    <div class="app-register">
         <!--  -->
        <mt-header>
            <router-link to="login" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
            <p slot="right"></p>
        </mt-header>
        <!--  -->
        <div class="login-box" style="background: url('/img/details/det-23/det-6.jpg') no-repeat">
            <div class="login-image">
                <log ref="myChild" @myParent="resRet" @getChild1="getVal1" @getChild2="getVal2" ></log>
                <div class="btn">
                    <span class="sText"></span>
                    <input type="button" value="" @click="Register">
                    <router-link to="login"></router-link>
                </div>   
            </div>
        </div>
    </div>
</template>
<script>
// 1.mint-ui Toast 
import {Toast} from "mint-ui"
import log from '@/components/log'
export default {
    components:{
        log
    },
     data(){
        return{
            childVal1:'',
            childVal2:'',
        }
        
    },
    methods:{
        // 
        resRet(){
            var phone1=this.childVal1;
            var url="retrieval?phone1="+phone1;
            this.axios.get(url).then(result=>{
                if(result.data.code==1){
                    Toast("");
                }
            })
        },
        // 
        jump(){
            this.$router.push("login")
        },
        // 
        getVal1(data){
            this.childVal1=data;
        },
        getVal2(data){
            this.childVal2=data;
        },
        Register(){
            // 
            var phone=this.childVal1;
            var upwd=this.childVal2;
            // 
            var regP=/^1[3-8][0-9]{9}$/;
            if(!regP.test(phone)){
                Toast("");
                return;
            }
            var regU=/^\w{6,20}$/;
            if(!regU.test(upwd)){
                Toast("");
                return;
            }
            var postData=this.qs.stringify({
                phone:phone,
                upwd:upwd,
            })
            var c=this.$refs.myChild.codeNumber();
            if(c==1){
                var url="register";
                this.axios.post(url,postData).then(result=>{
                    if(result.data==1 && c==1 && c!==""){
                        Toast(",3");
                        setTimeout(()=>{this.jump()},3000);
                    }else{
                        Toast("")
                    }
                })
            }else{
                Toast("")
                this.$refs.myChild.createCode();
            }
        },
        
    },
}
</script>




