import Vue from 'vue'
import App from './App.vue'
import router from './router'

import'mint-ui/lib/style.css'

Vue.config.productionTip = false

// 0.1Vuex
import Vuex from 'vuex'
// 0.2Vuex
Vue.use(Vuex)
// 0.3store
var store=new Vuex.Store({
  // 
  state:{
    comCount:sessionStorage.getItem("comCount")||0,
    watchCount:sessionStorage.getItem("watchCount")||0
  },
  // 
  mutations:{
    increment(state){
      state.comCount++;
      sessionStorage.setItem("comCount",state.comCount);
    },
    watch(state){
      state.watchCount++;
      sessionStorage.setItem("watchCount",state.watchCount);

    }
  },
  // 
  getters:{
    commentCount:function(state){
      return state.comCount;
    },
    watCount(state){
      return state.watchCount;
    }
  }
})

// 
import './lib/mui/css/header.css'
// 
import './lib/mui/css/login.css'
//5:  
//Vue.http.options.root = "http://127.0.0.1/vue_ser/";
//6:post  application/x-www-form-urlencoded
//Vue.http.options.emulateJSON = true;
//  MUI   Bootstrap 
import './lib/mui/css/mui.css'
//  MUI 
// 
import './lib/mui/css/icons-extra.css'
//import './lib/mui/js/mui.min.js'
// axios
import axios from "axios"
// qs  axios  postjs
import qs from "qs"
Vue.prototype.qs=qs;
// import mui from 'mui'

import './assets/icon/iconfont.css'

// sxiosvue
Vue.prototype.axios=axios;
// session
axios.defaults.withCredentials=true;


// 1.
import {Header,Button,Tabbar, TabItem} from "mint-ui";
import {Swipe,SwipeItem,Popup,InfiniteScroll,Search} from "mint-ui";
// 2.
Vue.component(Header.name,Header);
Vue.component(Button.name,Button);
Vue.component(Swipe.name,Swipe);
Vue.component(SwipeItem.name,SwipeItem);
Vue.component(Popup.name,Popup);
Vue.component(Search.name,Search);
Vue.use(InfiniteScroll);
Vue.component(Tabbar.name, Tabbar);
Vue.component(TabItem.name, TabItem)

// 7.
// val
Vue.filter("datetimeFilter",function(val){
  //7.1
  var now=new Date(val);
  // 7.2  
  var y=now.getFullYear();
  var m=now.getMonth();
  var d=now.getDate();
  // 7.3   
  var h=now.getHours();
  var mi=now.getMinutes();
  var s=now.getSeconds();
  // 7.4 100
  m<10&&(m="0"+m);
  d<10&&(d="0"+d);
  // 7.5
  return `${y}-${m}-${d} ${h}-${mi}-${s}`
})
new Vue({
  router,
  render: h => h(App),
  store,//0.4storeVue
}).$mount('#app')



