<template>
    <!--  -->
    <div class="app-login">
        <!--  -->
        <mt-header>
            <router-link to="personal" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
            <p slot="right"></p>
        </mt-header>
        <!--  -->
        <div class="login-box" style="background: url('/img/details/det-23/det-6.jpg') no-repeat">
            <div class="login-image">
                <log ref="myChild" @getChild1="getVal1" @getChild2="getVal2" ></log>
                <div class="btn">
                    <span class="sText"></span>
                    <input type="button" value="" @click="loginClick">
                    <router-link to="register"></router-link>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
// 1.mint-ui Toast 
import {Toast} from "mint-ui"
import log from '@/components/log'
export default {
    components:{
        log
    },
  
     data(){
        return{
            childVal1:'',
            childVal2:'',
        }
        
    },
    created(){
       
    },
    methods:{
        // 
        jump(){
            this.$router.push("/Home")
        },
        // 
        getVal1(data){
            this.childVal1=data;
        },
        getVal2(data){
            this.childVal2=data;
        },
        loginClick(){
            // 
            var phone=this.childVal1;
            var upwd=this.childVal2;
            var postData=this.qs.stringify({
                phone:phone,
                upwd:upwd,
            })
            var c=this.$refs.myChild.codeNumber();
            if(c==1){
                var url="login";
                this.axios.post(url,postData).then(result=>{
                    if(result.data==1 && c==1 && c!==""){
                        Toast(",3")
                        setTimeout(()=>{this.jump()},3000);
                        // 
                        var u=phone;
                        // sesionStorage
                        sessionStorage.setItem("phone",u)
                    }else{
                        Toast("")
                        this.$refs.myChild.createCode();
                    }
                    
                })
            }else{
                Toast("");
            }
        },
        
    },
}
</script>
<style>
   
    
</style>



