<template>
    <!-- -->
    <div class="app-pwd">
        <!--  -->
        <mt-header>
            <router-link to="set" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
            <p slot="right"></p>
        </mt-header>
        <!--  -->
        <div class="set-pwd">
            <div class="original">
                <span></span>
                <input v-model="upwd" name="upwd" type="password" placeholder="">
            </div>
            <div class="new-pwd">
                <span></span>
                <input v-model="upwd1" name="upwd1" type="password" placeholder="">
                <input v-model="upwd2" name="upwd2" type="password" placeholder="">
            </div>
            <div class="cancel">
                <p @click="clear"></p>
                <p @click="sub"></p>
            </div>
        </div>
    </div>
</template>
<script>
import {Toast} from "mint-ui"
export default {
    data(){
        return{
            upwd:"",//
            upwd1:"",//
            upwd2:"",//
        }
    },
    methods:{
        sub(){
            var upwd=this.upwd;
            var upwd1=this.upwd1;
            var reg=/^\w{6,20}$/;
            if(!reg.test(this.upwd1)){
                Toast("");
                return;
            }
            if(this.upwd1!=this.upwd2){
                Toast("");
                return;
            }
            var postData=this.qs.stringify({
                upwd:upwd,
                upwd1:upwd1
            })
            var url="pwd?upwd="+upwd+"&upwd1="+this.upwd1;
            this.axios.post(url,postData).then(result=>{
                if(result.data.code==1){
                    Toast("")
                    this.upwd="";
                    this.upwd1="";
                    this.upwd2="";
                }else{
                    Toast("")
                } 
            })
        },
        clear(){
            this.upwd="";
            this.upwd1="";
            this.upwd2="";
        },
    },
}
</script>
<style>
    /*  */
    .set-pwd{
        width: 85%;
        
        margin:0 auto;
        margin-top:40px;
    }
    /* span*/
    .set-pwd span{
        display:block;
        margin: 20px 0;
    }
    /* input */
    .set-pwd input{
        width: 100% !important;
        border-radius:7px !important;
    }
    /*  */
    .cancel{
        width: 100%;
        display: inline-block;
        display: flex;
        justify-content: space-between;
        margin-top:20px;
    }
    .cancel>p{
        width: 31%;
        padding: 13px 0;
        text-align: center;
        color:#333;
        font-size:20px;
    }
    .cancel>p:first-child{
        background: #dcdcdc;
    }
    .cancel>p:last-child{
        background: #2ad181bd;
    }
</style>



