<template>
    <!--  -->
    <div class="app-comment">
        <!--  -->
        <mt-header>
            <router-link to="/" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
            <p slot="right"></p>
        </mt-header>
        <!--  -->
        <div class="mui-card">
			<div class="mui-card-content">
				<div class="mui-card-content-inner">
					<textarea v-model="msg" placeholder="120"></textarea>
                    <mt-button @click="addComment" size="large" type="primary"></mt-button>
				</div>
			</div>
		</div>
        <!--  end-->
        <!--  start -->
        <div class="mui-card" v-for="(item,i) of list" :key=i>
			<div class="mui-card-content">
				<div class="mui-card-content-inner">
                    <div class="cmt-list">
                        <div class="cmt-item" >
                            <div>{{i+1}}  {{item.ctime | datetimeFilter}}</div>
                            <div>{{item.content}}</div>
                            <div class="zan">
                                <span ref="clickset" class="mui-icon-extra mui-icon-extra-like" @click.prevent="zan" :class="{active:isZan.active}" :data-id="item.id"></span>
                                <span >{{zanCound}}</span>
                            </div>
                        </div>
                    </div>
				</div>
			</div>
		</div>
    </div>
</template>
<script>
import {Toast} from "mint-ui"
export default {
    data(){
        return{
            msg:"",
            lid:this.$route.query.lid,
            list:[],//
            pno:0,//
            pageSize:5,//
            zanCound:0,//
            isZan:{
                active:false,
            },
        }
    },
    created(){
        this.loadMore();
        this.getCount();
    },
    methods:{
        addComment(){
            // 1.
            // 2.id
            var content=this.msg;
            var lid=this.lid;
            // 3.
            var size=content.trim().length;
            //4.
            if(size==0){
                Toast("");
                return;
            }
            // 5.postDatalid
            var postData=this.qs.stringify({
                lid:lid,
                content:content,
                
            })
            // 6.post
            var url="addcomment";
            this.axios.post(url,postData).then(result=>{
                // 7.
                if(result.data.code==1){
                    Toast("")
                }else{
                    Toast("")
                }
                // 8.
                this.msg="";
                // 9.
                this.pno=0;//
                this.list=[];//
                this.loadMore();
                     
            });
            var lid=this.lid;
            var url="addCount?lid="+lid;
                this.axios.get(url).then(result=>{
            })
        },
        loadMore(){
            // 1.pnopageSize
            this.pno++;
            var ps=this.pageSize;
            // 2.pnopageSize
            var reg=/^[0-9]{1,}$/;
            if(!reg.test(this.pno)){
                Toast("");
                return;
            }
            var url="getComment?lid="+this.lid+"&pno="+this.pno;
            this.axios.get(url).then(result=>{
                this.list=result.data.data;
            });    
        },
        // 
        zan(e){
            var cid=e.target.dataset.id;
            var url="zans?lid="+this.lid+"&cid="+cid;
            this.axios.get(url).then(result=>{
               if(result.data.code==-1){
                    Toast("");
                }else{
                    if(this.isZan.active==false && result.data.code==1){
                        Toast("");
                         this.isZan={
                            active:true
                        };
                       this.zanCound++;
                    }else{
                        Toast("")
                         this.isZan={
                            active:false
                        };
                        this.zanCound--;
                    } 
                }
            })
            
        },
       
        // 
        getCount(){
            var url="zanCount?lid="+this.lid;
            this.axios.get(url).then(result=>{
                  if(result.data.data.length>0){
                    this.zanCound=result.data.data[0].count;
                    this.isZan.active={
                        active:true
                    };
                }
            })
        },
    },
}
</script>
<style>
    .mint-button-text{
        height: 100%;
    }
    .app-comment .mint-button{
        height:35px;
    }
    .cmt-item div{
        display: inline-block;
    }
    .zan{
        position:relative;
        left:47%;
        top:-20px;
       
    }
    .zan>span:last-child{
        margin-left:7px;
    }
</style>



